.sidebar-right-trigger {
	position: absolute;
	z-index: 9;
	top: 9.75rem;
	right: 100%;
	background-color: var(--bs-primary);
	color: $white;
	display: inline-block;
	height: 3rem;
	width: 3rem;
	text-align: center;
	font-size: 1.75rem;
	line-height: 3rem;
	border-radius: 0.3125rem 0 0 0.3125rem;
	box-shadow: -0.3125rem 0.1875rem 0.3125rem 0rem rgba(119, 119, 119, 0.15);
	
	@include custommq ($max:64rem){
		display: none;
	}
	&:hover{
		color: $white;	
	}
	.fa-spin{
		--fa-animation-duration: 5s;
	}
}

.sidebar-right {
    right: -50rem;
    position: fixed;
    top: 0;
    width: 50rem;
    background-color: $body-bg;
    margin-top: 3.5rem;
    transition: all .5s ease-in-out;
	border-radius: var(--bs-border-radius);
	z-index: 9999;
	
	.bg-label-pattern{
		background: transparent;
		background-image: url('../images/pattern/pattern5.avif');
		background-size: 130%;
	}
	.bootstrap-select.dropdown{
		--bs-dropdown-bg: #{$body-bg};
		background-color: $body-bg;
		.dropdown-menu {
			--bs-dropdown-color: #{$body-color};
			--bs-dropdown-bg: var(--bs-body-bg);
			--bs-dropdown-border-color: var(--bs-border-color);
			--bs-dropdown-link-hover-color: var(--bs-primary);
			--bs-dropdown-link-hover-bg: #{$light};
			--bs-dropdown-link-color: #{$body-color};
			li{
				a:hover{
					background-color: rgba(var(--bs-primary-rgb), 0.1);
				}
			}
		}
	}
    .note-text{
		position: absolute;
		bottom: 0;
		left: 0;
		padding: 15px 20px;
		font-size: 12px;
	}
	.sidebar-close-trigger{
		position:absolute;
		z-index: 2;
		font-size: 1.75rem;
		top:0;
		right:-3rem;
		height: 3rem;
        width: 3rem;
		line-height:3rem;
		text-align:center;
		background: var(--bs-primary);
		color: $white;
	}
    &.show {
        right: 5.25rem;
		box-shadow: 0rem 0rem 3.125rem rgba(0 ,0 ,0 ,0.2);
        z-index: 9999;
		
		@include respond ('phone-land'){
			display:none;
		}
		.bg-overlay{
			position:fixed;
			width:100%;
			cursor:pointer;
			height:100%;
			top:0;
			left:0;
			background: rgba($black, 0.2);
			z-index: -1;
		}
    }
	.card-tabs{
		.nav-tabs {
			justify-content: space-between;
			position: sticky;
			top: 0;
			width:100%;
			background-color: var(--bs-body-bg);
			z-index: 2;
			border-bottom: 4px solid rgba(var(--bs-primary-rgb), 0.1);
			
			.nav-item {
				margin-bottom: 0;
				flex: 1;

				.nav-link {
					border: 0;
					font-size: 1.125rem;
					position: relative;
					text-align: center;
					background-color: var(--bs-body-bg);
					border-radius:0;
					margin:0;
					color: $body-color;
					
					&.active {
						background: rgba(var(--bs-primary-rgb), 0.1);
						color: var(--bs-primary);
					}
				}
			}
		}
	}
	.sidebar-right-inner > h4{
		padding: 0.625rem 1.25rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
		background-color: var(--bs-body-bg);
		margin: 0rem 0rem 0rem 0rem;
	}
    .tab-content {
        padding: 1.25rem 1.25rem 0;
		min-height: 450px;
		background-color: var(--bs-body-bg);
        
		.tab-pane {
            .admin-settings {
				.row > div {
					margin-bottom: 1.25rem;
				}
                p {
					margin-bottom: 0.5rem;
					font-size: 0.8125rem;
					font-weight: 500;
                }

                // All color switcher scss goes here
                input[type="radio"] {
                    display: none;

                    + label {
                        display: inline-block;
                        width: 2.1875rem;    
                        height: 2.1875rem;
                        cursor: pointer;
                        transition: all .1s ease;
						border-radius: 0.25rem;
						margin-right: 0.3125rem;
						margin-bottom: 0.1875rem;
                    }
                    &:checked {
                        + label {
                            position: relative;

                            &::after {
                                height: 2.0625rem;
                                width: 2.0625rem;
                                left: -0.25rem;
                                top: -0.25rem;
                                content: "";
                                position: absolute;
                                background-color: inherit;
								border-radius: 0.375rem;
								opacity: 0.4;
                            }
                        }
                    }
                }
            }

        }
    }   
    #nav_header, 
    #header, 
    #sidebar,
    #secondary,
	#primary{
        @each $name, $color in $theme_backgrounds {
            &_#{$name} {
                + label {
                    background-color: $color;            
                }
            }
        }
        &_color_1 {
            + label {
                border: 0.0625rem solid #c4c4c4;
            }
        }
    }
	
	&.style-1{
		height:100vh;
		width:15.625rem;
		margin-top:0;
		right:-15.625rem;
		
		.sidebar-right-inner{
			background: $white;
		}
		.sidebar-right-trigger {
			top: 12.4rem;
		}
		&.show{
			right:0;
			
			.sidebar-right-trigger{
				display: block;
			}
		}
	}
}
.nice-select.wide{
	line-height: 2.1125rem;
    
	&.form-control-sm{
		height: 35px;
		line-height: 1rem;
	}
}


@keyframes bounce {
	0% {
        transform: translateX(-8%);;
        -webkit-transform: translateX(-8%);; 
	}
	50% {
        transform: translateX(8%);;
        -webkit-transform: translateX(8%);; 
	}
	100% {
        transform: translateX(-8%);; 
        -webkit-transform: translateX(-8%);; 
	}
}
@-webkit-keyframes bounce {
	0% {
        transform: translateX(-8%);;
        -webkit-transform: translateX(-8%);; 
	}
	50% {
        transform: translateX(8%);;
        -webkit-transform: translateX(8%);; 
	}
	100% {
        transform: translateY(-8%);; 
        -webkit-transform: translateY(-8%);; 
	}
}
.nice-select.wide{
	@include respond ('laptop'){
		line-height: 2rem;
	}
}
[direction="rtl"] {
	.sidebar-right{
		left: -50rem;
		right: auto;
		&.show{
			left: 5.25rem;
			right: auto;
		}
		.sidebar-right-trigger{
			left: 100%;
			right: auto;
			border-radius: 0 0.3125rem 0.3125rem 0;
		}
		.sidebar-close-trigger{
			left: -3rem;
			right: auto;
		}
	}
}